.checkoutProduct {
    display: flex;
    margin: 0 20px;
}

.checkoutProduct>img {
    object-fit: contain;
    width: 180px;
    height: 180px;
}
.checkoutProduct__info{
    padding-left: 20px;
}
.checkoutProduct__rating{
    display:flex;
}
.checkoutProduct__title{
    font-size: 17px;
    font-weight: 800;
}
.checkoutProduct__info > button{
    background: #f0c14b;
    border:1px solid;
    margin-top: 10px;
    border-color:#a88734 #9c7e31 #846a29;
    color:#111;
}